@font-face {
    font-family: "Akira";
    src: url("fonts/Akira Expanded.otf") format("opentype");
}

@font-face {
    font-family: "Times Sans Serif";
    src: url("fonts/TIMESS.ttf") format("truetype");
}

@mixin padding{
    padding: {
        left:50;
        right: 50;
    }
    box-sizing: border-box;
}
@mixin gradient {
    background: linear-gradient(90deg, rgba(229,224,48,1) 0%, rgba(129,205,88,1) 10%, rgba(14,177,122,1) 20%, rgba(0,143,136,1) 30%, rgba(0,107,124,1) 40%, rgba(47,72,88,1) 50%, rgba(0,107,124,1) 60%, rgba(0,143,136,1) 70%, rgba(14,177,122,1) 80%, rgba(129,205,88,1) 90%, rgba(229,224,48,1) 100%);
    background-size: 200% auto;
    color: transparent;
    background-clip: text;
    animation: animate-gradient 30s linear infinite;
}
@keyframes animate-gradient {
    to {
      background-position: 200% center;
    }
}

* {
    margin:0;
    padding:0;
}

body {
    background: #e0e0e0;
    @include padding;
    padding-top:50;
    width: 100vw;
    height: 100dvh;
    max-height: 100dvh;
    overflow: hidden;
}

#overlay{
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(50px);
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    z-index: -1;
    opacity: 0;
    
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    transition: opacity 150ms ease-in-out;

    &.active{
        z-index: 5;
        opacity: 1;
    }

    h1 {
        font-family: 'Akira', sans-serif;
        font-size: 8rem;
        text-align: center;
        margin-bottom: 0;
    }

    h3 {
        font-family: 'Times Sans Serif', sans-serif;
        font-size: 2rem;
    }

    .colors{
        margin-bottom:20;
        border-radius: 50;
        overflow:hidden;
        width: 50%;
        height:100;
        display:flex;
        div{
            height: 100%;
            flex:1;
        }
    }
}

.neumorphic {
    box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
    border-radius:50;
}

header {
    background-color: rgba($color: #e0e0e0, $alpha: 0.5);

    @include padding;
    height:100;
    display:flex;
    flex-direction: column;
    align-items: stretch;
    position: absolute;
    z-index: 3;
    width: calc(100vw - 100px);
    transition: height 150ms ease-in-out;
    padding-top:25;
    backdrop-filter: blur(50px);
    overflow: hidden;

    #header-top{
        display:flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        #logo {
            font-family: 'Akira', sans-serif;
            font-size: 2.5rem;
            @include gradient;
            
        }
        a {
            text-decoration: none;
        }
        #menu-icon {
            height: 50;
            width: 50;
            @include gradient;
            font-variation-settings: 'FILL' 1, 'wght' 700, 'GRAD' 200, 'opsz' 48;
            font-size: 2rem;
            display:flex;
            align-items: center;
            justify-content: center;
        }
    }

    nav {
        //align-self: center;
        margin-top:calc(100dvh / 4);
        ul {
            align-items: center;
            justify-content: center;
            list-style: none;
            text-align: center;
            display:flex;
            gap:50;
        }
        a {text-decoration: none;color:black;}
        li {
            font-style:italic;
            font-family: "Times Sans Serif", sans-serif;
            font-size: 5rem;
            &.selected a {
                font-weight: bold;
                transition: all 150ms ease-in-out;
            }
        }

    }

    .footer{
        width: 100%;
        text-align: center;
        line-height: 40px;
        font-family: "Times Sans Serif", sans-serif;
        a {
            font-weight: bold;
            text-decoration: none;
            color:rgba(0,107,124,1);
            border-bottom: 2px dashed rgba(0,107,124,1);
        }
    }

    &.opened{
        height: calc(100dvh - 100px);
        nav {
            opacity: 1;
        }
    }
    
}

main{
    height:100%;
    padding-top:150;
    padding-bottom: 50;
    box-sizing: border-box;
    display:flex;
    flex-direction: column;

    #color-boxes{
        border-radius: 50px;
        display:flex;
        overflow: hidden;
        flex-grow: 1;
        margin-bottom: 50;
    }

    .color-box{
        flex:1;
        height: 100%;
    }

    #colorpicker{
        box-sizing: border-box;
        padding: {
            top:25;
            bottom:25;
        }
        overflow: hidden;
        width: 100%;
        display:flex;
        flex-direction: row;
        #range-groups{
            display:flex;
            flex-direction: column;
            flex-grow: 1;
            align-items: center;
            gap:20px;
            .range-group{
                @include padding;
                width: 100%;
                display:flex;
                flex-grow: 1;
                align-items: center;
                
                label{
                    text-align: right;
                    margin-right:10;
                    display:inline-block;
                    width: 50;
                    font-family: 'Times Sans Serif', sans-serif;
                }
                input[type=range]{
                    flex-grow: 1;
                }
            }
        }

        
        

        button{
            width: 20%;
            margin-right: 50;
            border: none;
            border-radius: 50;
            color: #444;
            font-size: 1.5rem;
            font-family: "Akira", sans-serif;
            background: #e0e0e0;
            outline: none;
            cursor: pointer;
            transition: .2s ease-in-out;
            box-shadow: -6px -6px 14px rgba(255, 255, 255, .7),
                        -6px -6px 10px rgba(255, 255, 255, .5),
                        6px 6px 8px rgba(255, 255, 255, .075),
                        6px 6px 10px rgba(0, 0, 0, .15);
            }
            button:hover {
                box-shadow: -2px -2px 6px rgba(255, 255, 255, .6),
                            -2px -2px 4px rgba(255, 255, 255, .4),
                            2px 2px 2px rgba(255, 255, 255, .05),
                            2px 2px 4px rgba(0, 0, 0, .1);
            }
            button:active {
                box-shadow: inset -2px -2px 6px rgba(255, 255, 255, .7),
                            inset -2px -2px 4px rgba(255, 255, 255, .5),
                            inset 2px 2px 2px rgba(255, 255, 255, .075),
                            inset 2px 2px 4px rgba(0, 0, 0, .15);
            }
    }
}

@media (max-width: 1024px){
    #overlay h1{
        font-size: 5rem;
    }
}

@media (max-width: 800px){
    #overlay h1{
        font-size: 2rem;
        margin-bottom:30;
    }

    header #header-top #logo{
        font-size: 1.5rem;
    }

    header nav ul{
        flex-direction: column;
        gap:0;
    }

    main #colorpicker {
        #range-groups .range-group{
            padding: {
                left:0;
                right: 25;
            }
        }
        button{
            margin-right:25;
            font-size: 0.7rem;
        }
    }
}



    